import React,{useState} from 'react'
import './Contact.css'
const Contact = () => {
    const [name,setName]=useState('');
    const [email,setEmail]=useState('');
    const [phone,setPhoneNumber]=useState();
const handleSubmit=()=>{
   
}
    
  return (
  <div>
      <form onSubmit={handleSubmit} className="input-form">
          <h1 className="input-title">Register</h1>
          <input className="input-name" placeholder="Name" type="text" value={name} onChange={(event)=>{setName(event.target.value)}}></input>
          <input className="input-email" placeholder="Email" type="email" value={email} onChange={(event)=>{setEmail(event.target.value)}}></input>
          <input className="input-phone" placeholder="Phone Number" type="phone" value={phone} onChange={(event)=>{setPhoneNumber(event.target.value)}}></input>
          <button className="input-button" type="submit">Submit</button>
      </form>
  </div>
  )
}

export default Contact
