import React from 'react';
import './Home.css';
const Home = (props) => {
 const handleGallery=()=>{
props.history.push("/gallery");
  }
  const handleService=()=>{
    props.history.push("/services")
  }
  return (
    <div className="home">
        <div className="home-image">
            <p className="home-title">Creating<br></br> Unforgettable<br></br>Memories</p>
        </div>
        <div className="home-Services">
  <div className="home-service-detail">
    <h1 className="home-services-title">SERVICES</h1>
    <p className="home-service-paragraph">Turn your dream<br></br>into a reality</p>
    <button className="home-service-button" onClick={handleService}>DISCOVER</button>
</div>
<div className="home-service-image"></div>

        </div>
        <div className="main-gallery">
        <div className="home-service-gallery">
    <div className="main-gallery-details">
<h1 className="main-gallery-details-title">GALLERY</h1>
<p className="main-gallery-details-paragraph">Unforgettable <br></br>Memories</p>

<button className="main-gallery-details-button" onClick={handleGallery}>DISCOVER</button>
    </div>
    </div>
        </div>
        
    </div>
  )
}

export default Home;
