import React,{useState} from 'react'
import axios from 'axios';
import './Gallery.css';
const Gallerys = () => {
const [photo,setPhotos]=useState([]);
useState(()=>{
axios.get("https://picsum.photos/v2/list")
.then(res=>{

  var url=res.data.map(urls=>{
return urls.download_url;
  });
setPhotos(url);

}).catch((err)=>{
  console.log(err);
})

},[])
const gallery=
  photo.map(url=>
<img src={url} alt="" className="gallery"/>
  )

  return (
  <div >
    {gallery}

  </div>
  )
}

export default Gallerys;