import React from 'react'
import {Link} from 'react-router-dom';
import './Navigation.css';
const Navigation = () => {
  return (
    <header className="nav-main">
 <Link to="/home" className="nav-title">EVENTECH'S</Link>
 <Link to="/home" className="nav-home">Home</Link>
 <Link to="/services" className="nav-items">Services</Link>
 <Link to="/about" className="nav-items">About</Link>
 <Link to="/gallery" className="nav-items">Gallery</Link>
 <Link to="/contact" className="nav-items">Contact</Link>
    </header>
  )
}

export default Navigation;